import { Box } from '@material-ui/core';
import { CognitoUser } from 'amazon-cognito-identity-js';
import { CognitoHostedUIIdentityProvider } from "@aws-amplify/auth/lib/types";
import React, { useCallback, useState } from 'react';
import { connect } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import { AnyAction } from 'redux';
import { ThunkDispatch } from 'redux-thunk';
import { EmailOrPhone } from '../ui/EmailOrPhone';
import { IconGoogle,IconFacebook } from '../ui/Icons';
import { Loader } from '../ui/Loader';
import { SingleLineInput } from '../ui/SingleLineInput';
import { useUrlQuery } from '../../core/hooks';
import { Validators } from '../../core/validators';
import { __tr } from '../../i18n';
import { Routes } from '../../routes';
import { IAppState } from '../../state';
import { initializeState } from '../../state/middlewares';
import { Auth } from 'aws-amplify';

import styles from '../../styles/Login.module.scss';
import { useEffect } from 'react';

const mapState = (state: IAppState) => {
	return {
		user: state.userState.user,
	};
};

const mapDispatch = (dispatch: ThunkDispatch<IAppState, {}, AnyAction>) => {
	return {
		initialize: () => dispatch(initializeState()),
	};
};

type Props = ReturnType<typeof mapState> & ReturnType<typeof mapDispatch>;

function Login(props: Props) {
	const [loading, setLoading] = useState(false);
	const [emailOrPhone, setEmailOrPhone] = useState('');
	const [errors, setErrors] = useState<any>({});

	const history = useHistory();
	const nextSegment = useUrlQuery('next', '');

	const onSubmit = useCallback(
		async (ev: React.ChangeEvent<HTMLFormElement>) => {
		  ev.preventDefault();
	
		  // let form = ev.currentTarget;
		  let data = {
			emailOrPhone,
			// password: form.password.value,
		  };
	
		  let errs: any = {};
		  if (!Validators.isEmailOrPhone(data.emailOrPhone)) {
			errs.emailOrPhone = __tr('errorInvalidEmailOrPhone');
		  }
	
		  // if (!data.password) {
		  //   errs.password = __tr('errorInvalidPassword');
		  // }
	
		  setErrors(errs);
	
		  if (Object.keys(errs).length > 0) {
			return;
		  }
	
		  setLoading(true);
	
		  // checks if user already exists or not -manish
		  const code = '000000';
		  Auth.confirmSignUp(emailOrPhone, code, {
			// If set to False, the API will throw an AliasExistsException error if the phone number/email used already exists as an alias with a different user
			forceAliasCreation: false,
		  })
			.then((data) => {
			  console.log(data);
			  setLoading(false);
			})
			.catch((err) => {
			  switch (err.code) {
				case 'UserNotFoundException':
				  errs.emailOrPhone = __tr(
					emailOrPhone.startsWith('+') ? 'errorInvalidPhoneNumber' : 'errorInvalidEmail'
				  );
				  setErrors(errs);
				  setLoading(false);
				  return true;
				case 'NotAuthorizedException':
				  history.push(
					`/auth/login/password?${emailOrPhone.startsWith('+') ? 'phone' : 'email'}=${emailOrPhone}`
				  );
				  setLoading(false);
				  return false;
				case 'AliasExistsException':
				  // Email alias already exists
				  history.push(
					`/auth/login/password?${emailOrPhone.startsWith('+') ? 'phone' : 'email'}=${emailOrPhone}`
				  );
				  setLoading(false);
				  return true;
				case 'CodeMismatchException':
				  return false;
				case 'ExpiredCodeException':
				  return false;
				default:
				  return false;
			  }
			});
		},
		[emailOrPhone, history]
	  );
	
	useEffect(() => {
		if (props.user) {
			history.replace(nextSegment);
		  }
		}, [history, props, nextSegment]);

		const googleLogin = () => {
			Auth.federatedSignIn({provider: CognitoHostedUIIdentityProvider.Google}).then(res=>{
			props.initialize();
			}).catch((err) =>{
			  console.log('err',err);
			});
		};
	  
		const facebookLogin = () => {
			Auth.federatedSignIn({provider: CognitoHostedUIIdentityProvider.Facebook}).then(res=>{
			props.initialize();
			}).catch((err) =>{
			  console.log('err',err);
			});
		};

	return (
		<div className={styles.loginWrapper}>
			<h3>Welcome to Wobb!</h3>
			<div className={styles.formWrapper}>
				<h3>Sign in</h3>
				<form action='' className={styles.form} onSubmit={onSubmit}>
					{loading && <Loader />}
					<Box marginY={2}>
						<EmailOrPhone
							label='Email or phone number'
							placeholder='john@example.com'
							onChange={val => setEmailOrPhone(val.value)}
							error={errors.emailOrPhone}
						/>
					</Box>
					{/* <Box marginY={2}>
						<SingleLineInput
							label='Password'
							type='password'
							name='password'
							placeholder='Must be at least 6 characters'
							error={errors.password}
						/>
					</Box>
					<Box marginY={2}>
						<Link
							to={Routes.ForgotPassword}
							className={styles.passwordRecoveryLink}
						>
							Forgot Password ?
						</Link>
					</Box> */}
					<Box marginY={2}>
						<button type='submit' className={styles.btnSubmit}>
							Next
						</button>
					</Box>
					<Box
						marginY={4}
						display='flex'
						flexDirection='row'
						alignItems='center'
						justifyContent='center'
					>
						<div className={styles.lined}>
							<div className={styles.text}>Or</div>
						</div>
					</Box>
					<Box marginY={1}>
						<button type='button' 
						onClick={() => {googleLogin()}}
						className={styles.googleSignin}>
							<span className={styles.icon}>
								<IconGoogle size={24} />
							</span>
							<span className={styles.text}>Continue with Google</span>
						</button>
					</Box>
					<Box marginY={1}>
						<button type='button' 
						onClick={() => {facebookLogin()}}
						className={styles.googleSignin}>
							<span className={styles.icon}>
								<IconFacebook size={24} />
							</span>
							<span className={styles.text}>Continue with Facebook</span>
						</button>
					</Box>
					<Box
						marginY={2}
						display='flex'
						flexDirection='row'
						alignItems='center'
						justifyContent='center'
					>
						<span className={styles.callToSignup}>New to Wobb ?</span>
						<Link to={Routes.Signup} className={styles.signupLink}>
							Sign up
						</Link>
					</Box>
				</form>
			</div>
		</div>
	);
}

export default connect(mapState, mapDispatch)(Login);
